import React, { useState, useEffect } from 'react';
import {
  Box,
  Text,
  Button,
  Stat,
  StatLabel,
  StatNumber,
  StatHelpText,
  HStack,
  Grid,
} from '@chakra-ui/react';
import { ColorModeSwitcher } from './ColorModeSwitcher';

const Web3 = require('web3');
// let web3 = await new Web3(Web3.givenProvider);
// await web3.eth.net.getId().then(console.log);

const Moralis = require('moralis');

function App() {
  Moralis.initialize('9jTSEy7eKGdH8EBCOvH5zCYM01TxksUQcItcbfQg');
  Moralis.serverURL = 'https://dzfl3tiplyqt.usemoralis.com:2053/server';

  const [userAddress, setUserAddress] = useState(null);
  const [balances, setBalances] = useState(null);
  const [balancesNFT, setBalancesNFT] = useState(null);
  const [netID, setNetID] = useState(null);
  let currentUser = Moralis.User.current();

  const login = async () => {
    try {
      Moralis.Web3.authenticate().then(function (user) {
        setUserAddress(user.get('ethAddress'));
        console.log(user);
      });
    } catch (error) {
      console.log(error);
    }
  };

  const logout = async () => {
    try {
      Moralis.User.logOut().then(() => {
        currentUser = Moralis.User.current();
        setUserAddress(null);
      });
    } catch (error) {
      console.log(error);
    }
  };

  const getBalances = async () => {
    if (userAddress) {
      try {
        const options = { chain: 'bsc testnet', address: userAddress };
        const tokens = await Moralis.Web3.getAllERC20(options);
        setBalances(tokens);
        console.log(balances);
      } catch (error) {
        console.log(error);
      }
    }
  };

  const getNFT = async () => {
    try {
      const options = { chain: 'bsc testnet', address: userAddress };
      const BscNFT = await Moralis.Web3.getNFTs(options);
      setBalancesNFT(BscNFT);
      console.log(BscNFT);
    } catch (error) {
      console.log(error);
    }
  };

  const showCurrentUser = () => {
    if (currentUser) {
      console.log(currentUser.get('ethAddress'));
    }
  };

  useEffect(() => {
    if (currentUser) {
      setUserAddress(currentUser.get('ethAddress'));
    }
  }, []);

  return (
    <Box textAlign="center" fontSize="xl">
      <Grid minH="100vh" p={3}>
        <HStack>
          <ColorModeSwitcher justifySelf="flex-end" />
          {!currentUser ? (
            <Button justifySelf="flex-end" onClick={login}>
              Login
            </Button>
          ) : (
            <Button justifySelf="flex-end" onClick={logout}>
              Logout
            </Button>
          )}
          <Button onClick={getBalances}>Balances</Button>
          <Button onClick={getNFT}>NFT Balances</Button>
          <Button onClick={showCurrentUser}> Current User</Button>
        </HStack>
        <HStack spacing={8}>
          <Text>Wallet Balances</Text>
          {netID && <Text>{netID} </Text>}

          {balances &&
            balances.map(({ balance, decimals, name, symbol }) => {
              const decimales = Number(`1e${decimals}`);
              const amount = (balance / decimales).toFixed(2);
              return (
                <Stat>
                  <StatLabel>{symbol}</StatLabel>
                  <StatNumber> {amount} </StatNumber>
                  <StatHelpText>{name}</StatHelpText>
                </Stat>
              );
            })}
        </HStack>
      </Grid>
    </Box>
  );
}

export default App;
