import React, { useState, useEffect } from 'react';
import NftToken from './build/NFT';
import {
  Container,
  Text,
  Flex,
  Input,
  Button,
  Divider,
  Box,
  Stack,
  Image,
  Center,
  Spinner,
  VStack,
} from '@chakra-ui/react';
import { ColorModeSwitcher } from './ColorModeSwitcher';
import web3 from './build/web3';

const Moralis = require('moralis');

function NFTMarket() {
  // Initialize Moralis for web3 tools
  Moralis.initialize('9jTSEy7eKGdH8EBCOvH5zCYM01TxksUQcItcbfQg');
  Moralis.serverURL = 'https://dzfl3tiplyqt.usemoralis.com:2053/server';

  // Set Variables
  const [userAddress, setUserAddress] = useState(null);
  const [balances, setBalances] = useState(null);
  const [balancesNFT, setBalancesNFT] = useState(null);
  const [isUploading, setIsUploading] = useState(false);
  const [fileUploaded, setFileUploaded] = useState(null);
  const [fileName, setFileName] = useState('');
  const [fileURI, setFileURI] = useState(null);
  const [mediaType, setMediaType] = useState(null);
  let currentUser = Moralis.User.current();

  // Login with Moralis Function
  const login = async () => {
    try {
      Moralis.Web3.authenticate().then(function (user) {
        setUserAddress(user.get('ethAddress'));
      });
    } catch (error) {
      console.log(error);
    }
  };

  // Logout from Moralis
  const logout = async () => {
    try {
      Moralis.User.logOut().then(() => {
        currentUser = Moralis.User.current();
        setUserAddress(null);
      });
    } catch (error) {
      console.log(error);
    }
  };

  // Get BEP-20 balances of userAddress
  const getBalances = async () => {
    if (userAddress) {
      try {
        const options = { chain: 'bsc testnet', address: userAddress };
        const tokens = await Moralis.Web3.getAllERC20(options);
        setBalances(tokens);
        console.log(balances);
      } catch (error) {
        console.log(error);
      }
    }
  };

  // Get NFT Balances of userAddress
  const getNFTBalance = async () => {
    try {
      const options = {
        chain: 'bsc testnet',
        address: window.ethereum.selectedAddress,
      };
      const BscNFT = await Moralis.Web3.getNFTs(options);
      setBalancesNFT(BscNFT);
      console.log(BscNFT);
    } catch (error) {
      console.log(error);
    }
  };

  //Upload File to IPFS
  async function uploadFile(e) {
    setIsUploading(true);
    try {
      const data = e.target.files[0];
      const fileToUpload = new Moralis.File(data.name, data);
      console.log(data, fileToUpload);
      await fileToUpload.saveIPFS();
      setFileUploaded(fileToUpload._ipfs);
      setFileURI(fileToUpload._hash);
      if (data.type.includes('video') || data.type.includes('audio')) {
        setMediaType('video');
      } else if (data.type.includes('image')) {
        setMediaType('image');
      }
    } catch (error) {
      console.log(error);
    }
    setIsUploading(false);
  }

  // Send transaction to contract
  const createNFT = async () => {
    try {
      let instance = await NftToken();
      const accounts = await web3.eth.getAccounts();
      const newNFT = await instance.methods
        .create(fileURI, fileName)
        .send({ from: window.ethereum.selectedAddress });
    } catch (error) {
      console.log(error);
    }
  };

  // Get current user in case of page reload - don't need to login every time
  useEffect(() => {
    if (currentUser) {
      setUserAddress(currentUser.get('ethAddress'));
    }
  }, []);

  return (
    <Container maxW="container.xl">
      {/* Navbar with login and getNFT buttons */}

      <Flex>
        <Button onClick={getNFTBalance}>My NFT's</Button>
        <Divider />
        {userAddress ? (
          <Button onClick={logout} align="left">
            Logout
          </Button>
        ) : (
          <Button onClick={login} align="left">
            Login
          </Button>
        )}
      </Flex>

      {/* Create NFT form */}

      <Stack spacing={8} direction="row" justify="center">
        <Box maxW="md" p={4}>
          <Text>Upload File</Text>
          <VStack p={1} borderWidth="2px" borderRadius="lg" minH="100px">
            <label className="label-upload">
              Upload
              <input
                onChange={e => uploadFile(e)}
                type="file"
                style={{ display: 'none' }}
              />
            </label>

            <Divider margin={3} />
            <br />
            <Text> PNG, GIF, WEBP, MP4 or MP3. Max 30mb</Text>
          </VStack>

          <Divider margin={3} />

          <Text> Name </Text>
          <Input
            onChange={e => setFileName(e.target.value)}
            value={fileName}
            type="text"
            id="name"
          />

          <Divider margin={3} />

          <Text> Description </Text>
          <Input type="text" name="description" id="description" />

          <Divider margin={3} />

          <Text> Properties </Text>
          <Input type="text" name="properties" id="properties" />

          <Divider margin={3} />

          <Button isFullWidth="true" onClick={createNFT}>
            Create!
          </Button>
        </Box>

        {/* NFT Preview */}

        <VStack>
          <Text> Preview </Text>
          <Center
            maxW="sm"
            minW="sm"
            minH="sm"
            w="100%"
            p={1}
            borderWidth="2px"
            borderRadius="lg"
          >
            {fileUploaded ? (
              mediaType === 'video' ? (
                <video controls="controls" src={fileUploaded} />
              ) : (
                mediaType === 'image' && (
                  <Image controls="controls" src={fileUploaded} />
                )
              )
            ) : isUploading ? (
              <Spinner />
            ) : (
              <Text>Select a file to upload</Text>
            )}
          </Center>
        </VStack>
      </Stack>
    </Container>
  );
}

export default NFTMarket;
